.btn{

    height: rem(40);
    color: #fff;
    border: none;
    padding: 0 rem(30);
    color: $dark;
    background: $lgray;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: none;

    &:active,
    &:focus{
        border: none;
        outline: none;
    }

    &.btn-red{
        color: #fff;
        background-color: $red;

        &:hover{
            background-color: #d31417;
        }
    }

    &.btn-dark{
        color: #fff;
        background-color: $dark;

        &:hover{
            opacity: 0.9;
        }
    }

    &.btn-line{
        border: 1px solid #000000;
        background: none;
        text-transform: uppercase;
        font-weight: bold;

        &:hover{
            background-color: #d31417;
            color: #fff !important;
            border-color: #d31417;
        }
    }

    &.btn-line-radius{
        background: transparent;
        border: 1px solid $mgray;
        border-radius: rem(40);

        &:hover{
            border-color: $red;
        }
    }

    &.btn-full{
        width: 100%;
    }

    &.btn-bold{
        font-weight: bold;
        text-transform: uppercase;
        letter-spacing: rem(1)
    }
}