.header-top {
    border-bottom: 1px solid $lgray;

    @media screen and (max-width: $w760) {
        padding-top: 5px;
        padding-bottom: 5px;
    }



    & .container-1770 {
        display: flex;
        height: 40px;

        @media screen and (max-width: 1210px) {
            height: auto;
            padding-top: 5px;
            padding-bottom: 5px;
        }

        &>div {
            flex-grow: 1;
        }
    }

    &__info,
    &__phones {
        display: flex;
        /* @media screen and (max-width: 1210px) {
            flex-direction: column;
        } */
    }

    &__wishlist,
    &__find {
        display: flex;
        align-items: center;
        margin-left: 30px;
        cursor: pointer;


        &:hover {

            color: $red;

            & svg {
                fill: $red;
            }
        }

        & svg {
            fill: $gray;
            margin-right: rem(10);
        }
    }

    &__find {
        margin-left: auto;
    }

    &__info {
        & .header-info {

            cursor: pointer;

            &:first-child {
                @media screen and (max-width: $w1280) {
                    display: none;
                }
            }

            @media screen and (max-width: rem(680)) {
                display: none;
            }
        }
    }

    &__phones {
        justify-content: flex-end
    }

    & .header-info {
        display: flex;
        align-items: center;
        margin-right: rem(30);

        &>div {
            display: flex;
            align-items: center;
        }

        &__icon {
            margin-right: rem(10);

            & svg {
                fill: #bbb;
            }
        }

        &__text {
            & span {
                border: 1px solid $lgray;
                display: inline-block;
                padding: rem(2) rem(5);
                margin-left: rem(10);
                font-size: 13px;
                color: $gray;
                border-radius: rem(5);
            }
        }

        &:first-child {
            margin-left: rem(265);
            transition: 0.4s ease;

            @media screen and (max-width: $w1500) {
                margin-left: rem(180);
            }

            @media screen and (max-width: $w1440) {
                margin-left: rem(137);
            }

            @media screen and (max-width: $w1376) {
                margin-left: rem(0);
            }
        }
    }

    & .header-phone {
        display: flex;
        margin-right: rem(30);
        cursor: pointer;

        @media screen and (max-width: 1210px) {
            justify-content: flex-end;
            margin-right: rem(15);
        }

        &>div {
            display: flex;
            align-items: center;
        }

        &__text {
            margin-right: 10px;
            color: $gray;

            @media screen and (max-width: $w940) {
                display: none !important;
            }
        }

        &:last-child {
            margin-right: 0;
        }
    }

    & .header-info__arr,
    & .header-phone__arr {
        margin-left: 10px;
    }
}

/* header-main */

.header-main {

    border-bottom: 1px solid $lgray;

    &.fixed {
        position: fixed;
        top: 0;
        background: #fff;
        left: 0;
        right: 0;
        z-index: 10;

        & .header-main__logo {

            @media screen and (min-width: $w760) {
                max-width: rem(100);

                & img {
                    top: 0;
                    margin-top: 0;
                    padding: 10px;
                }
            }
        }
    }

    &__logo {

        max-width: rem(150);
        flex-grow: 1;
        margin-right: rem(110);
        transition: 0.2s linear;

        @media screen and (max-width: $w1500) {
            max-width: rem(100);
            padding: rem(10) 0;
            margin-right: rem(74);
        }

        @media screen and (max-width: $w1440) {
            margin-right: rem(30);
        }

        @media screen and (max-width: $w1280) {
            max-width: rem(80);
            min-width: rem(70);
        }

        @media screen and (max-width: $w940) {
            max-width: rem(55);
            min-width: rem(55);
            margin-right: rem(20);
        }

        & img {

            @media screen and (min-width: $w1500) {
                position: relative;
                top: rem(-14);
                margin-top: rem(-10);
            }


        }
    }

    & .container-1770 {
        display: flex;
        align-items: center;
        position: relative;
    }

    &__nav {

        display: flex;
        align-items: center;
        flex-grow: 1;



        & nav {
            display: flex;

            @media screen and (max-width: $w580) {
                display: none;
            }

            & ul {
                display: flex;
                list-style-type: none;
                margin-left: 10px;

                & li {

                    margin-right: rem(40);

                    @media screen and (max-width: $w940) {
                        margin-right: rem(20);
                    }

                    @media screen and (max-width: $w760) {
                        display: none;
                    }

                    &:first-child {
                        @media screen and (max-width: $w760) {
                            display: block;
                        }
                    }

                    & a {
                        font-style: normal;
                        font-weight: bold;
                        font-size: 16px;

                        &:hover {
                            color: $red;
                        }
                    }
                }
            }
        }

    }

    &__fav {
        & a {
            display: flex;
            align-items: center;

            &:hover {
                fill: $red;
            }
        }
    }

    &__action {
        display: flex;
        align-items: center;
        height: rem(40);

        & div {
            display: flex;
            align-items: center;
            border-left: 1px solid $lgray;
            height: 100%;
            padding: 0 rem(20);

            @media screen and (max-width: $w580) {
                padding: 0 rem(15);
            }

            &:first-child {
                @media screen and (min-width: $w580) {
                    border-left: 0;
                }
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }

    &__cart {
        & a {
            display: flex;
            align-items: center;
            color: $red;
            font-size: rem(15);

            & span {
                padding: 0 5px;
            }

            & svg {
                fill: $red;
                margin-right: rem(10);
            }
        }
    }

    &__login {

        & a {
            display: flex;
            align-items: center;
            font-size: rem(15);

            & svg {
                margin-right: 10px;
            }

            & span {
                @media screen and (max-width: $w480) {
                    display: none;
                }
            }
        }
    }

    .nav__search {
        display: flex;
        align-items: center;

        & span {
            display: flex;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
        }


        &:hover {
            & svg {
                fill: $red;
            }
        }



        &.open {
            flex-grow: 1;

            & .form {
                margin-left: rem(20);
                width: 100%;
            }

            @media screen and (max-width: $w1100) {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                background: #fff;
                padding: 0 rem(20);
            }
        }

        @media screen and (max-width: $w760) {
            margin-left: auto;
        }

        @media screen and (max-width: $w580) {
            padding-right: rem(15);
        }
    }

    .text__hamburger {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        margin-left: 10px;
        margin-right: 20px;
        display: flex;
        align-items: center;
    }

    &__phone {
        margin-right: rem(55);


        @media screen and (max-width: $w1280) {
            margin-right: 30px;
        }

        @media screen and (max-width: $w760) {
            margin-right: 0;
            margin-left: 25px;

            &--consult {
                display: none;
            }
        }
    }

    &__btn {

        @media screen and (max-width: $w1024) {
            display: none;
        }

        & .btn {
            @media screen and (min-width: $w1280) {
                min-width: rem(245);
                height: 44px;
            }
        }
    }



}

.header-phone {

    &__number {

        text-align: right;

        & a {
            font-size: rem(18);

            @media screen and (max-width: $w1100) {
                font-size: 14px;
                font-weight: 600;
            }
        }
    }

    &__title {
        color: $gray;
        text-align: right;
        margin-bottom: 5px;

        @media screen and (max-width: $w1100) {
            margin-bottom: 3px;

        }
    }
}

.cart-text,
.cart-price {
    @media screen and (max-width: $w1024) {
        display: none;
    }
}

.cart-count {
    @media screen and (max-width: 370px) {
        display: none;
    }
}

/* burger */

$scale: 1.5;

$burger_w: rem(50/$scale);
$burger_h: rem(50/$scale);
$line_w: rem(30/$scale);
$line_h: rem(2);
$line_mrg_1: rem(-(11/$scale));
$line_mrg_2: rem(-(2/$scale));
$line_mrg_3: rem((7/$scale));
$line_offset: rem((9/$scale));
$left: rem(10/$scale);

.nav__hamburger {
    position: relative;
    display: none;
    width: $burger_w;
    height: $burger_h;
    margin-right: rem(10);
    -webkit-transition: -webkit-transform .3s cubic-bezier(.4, 0, .2, 1);
    transition: -webkit-transform .3s cubic-bezier(.4, 0, .2, 1);
    transition: transform .3s cubic-bezier(.4, 0, .2, 1);
    transition: transform .3s cubic-bezier(.4, 0, .2, 1), -webkit-transform .3s cubic-bezier(.4, 0, .2, 1);
    display: block;

    border: 0;
    margin: 0;
    padding: 0;
    text-align: inherit;
    text-transform: inherit;
    font: inherit;
    letter-spacing: inherit;
    background: none;
    cursor: pointer;
    overflow: visible;

    & i:first-of-type,
    & i:nth-of-type(3) {
        content: "";
        margin-top: $line_mrg_1;
        -webkit-transition: opacity .15s 50ms, -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) .2s;
        transition: opacity .15s 50ms, -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) .2s;
        transition: transform .2s cubic-bezier(.4, 0, .2, 1) .2s, opacity .15s 50ms;
        transition: transform .2s cubic-bezier(.4, 0, .2, 1) .2s, opacity .15s 50ms, -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) .2s;
    }

    & i:first-of-type,
    & i:nth-of-type(2),
    & i:nth-of-type(3) {
        position: absolute;
        left: $left;
        top: 50%;
        width: $line_w;
        height: $line_h;
        background: #000;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }

    & i:first-of-type,
    & i:nth-of-type(3) {
        content: "";
        margin-top: $line_mrg_1;
        -webkit-transition: opacity 0.15s 50ms, -webkit-transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
        transition: opacity 0.15s 50ms, -webkit-transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
        transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.2s, opacity 0.15s 50ms;
        transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.2s, opacity 0.15s 50ms, -webkit-transform 0.2s cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
    }

    & i:nth-of-type(2) {
        margin-top: $line_mrg_2;
        display: block;
        -webkit-transition: -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) 50ms;
        transition: -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) 50ms;
        transition: transform .2s cubic-bezier(.4, 0, .2, 1) 50ms;
        transition: transform .2s cubic-bezier(.4, 0, .2, 1) 50ms, -webkit-transform .2s cubic-bezier(.4, 0, .2, 1) 50ms;
    }

    & i:nth-of-type(3) {
        margin-top: $line_mrg_3;
    }

    &.open {
        -webkit-transform: rotate(135deg);
        transform: rotate(135deg);
        -webkit-transition-delay: .2s;
        transition-delay: .2s;

        & i:first-of-type {
            -webkit-transform: translateY($line_offset);
            transform: translateY($line_offset);
            -webkit-transition-delay: 0s, .2s;
            transition-delay: 0s, .2s;
        }

        & i:nth-of-type(2) {
            -webkit-transition-delay: .15s;
            transition-delay: .15s;
            -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
        }

        & i:nth-of-type(3) {
            -webkit-transform: translateY(-$line_offset);
            transform: translateY(-$line_offset);
            opacity: 0;
            -webkit-transition-delay: 0s, .15s;
            transition-delay: 0s, .15s;
        }
    }


}

button.main-menu__hamburger:active,
button.main-menu__hamburger:focus {
    border: none !important;
    outline: none !important;
}

.phone-list {
    & li {
        display: flex;
        flex-direction: column;
        list-style-type: none;
        padding: rem(10) 0;
    }

    &>* {
        display: block;
    }

    & span {
        color: $gray;
        margin-bottom: rem(5);
    }

    & a {

        font-weight: bold;
        font-size: rem(16);
        font-weight: bold;
        font-weight: bold;

    }
}


.header-menu-info {
    list-style-type: none;

    & li {
        & a {
            padding: 10px 0;
            display: block;
            border-bottom: 1px solid #eee;

            &.active,
            &:hover {
                border-color: $red;
            }

        }

        &:last-child a {
            border-color: transparent;

            &:hover {
                border-color: $red;
            }
        }
    }
}